.ProductBlock {
    flex: 1 1 30%;
    text-align: center;

    &:hover {
        border: solid #f5bd07 2px;
        opacity: 0.5%;
    }

    &:hover .HoverText {
        display: block;
        color: white;
        font-size: 1.5vw;
        font-weight: bold;
    }
}

.Image {
    margin-top: 0.5%;
    width: 100%;
    height: 40vh;
    margin-bottom: 3%;
    object-fit: contain;
}

.TextOnImage {
    position: absolute;
    right: 50%;
    left: 50%;
    bottom: 15%;  
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.2vw;
    background-color: hsla(46, 94%, 49%, 0.938);
}

.Price {
    color: #f5bd07;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.2vw;
    font-weight: bold;
}

.Title {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.5vw;
}

.HoverText {
    display: none;
}